html {
  box-sizing: border-box;
}

* ::before,
::after {
  box-sizing: inherit;
}

p,
span,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

body {
  background-color: #d1e7e0;
  color: #161718;
  font-family: "Montserrat", Arial, "sans-serif";
  font-size: 16px;
}

img {
  max-width: 100%;
}

.container {
  padding: 15px 20px;
}

.navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}

.navigation__logo {
  font-size: 30px;
  font-weight: 700;
}

.navigation__items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.navigation__item {
  color: #463558;
  padding: 8px;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
}
.navigation__item:hover {
  color: #d1e7e0;
  background-color: #463558;
  text-decoration: underline;
}

.about {
  text-align: center;
}

.about__headline {
  font-size: 28px;
  color: #161718;
  font-weight: 600;
  text-align: center;
}

.hero {
  background-image: url("/images/library.jpg");
  width: 1100px;
  height: 30vh;
  background-size: contain;
}

.hero--headline {
  font-size: 28px;
  color: #161718;
  font-weight: 600;
  color: #ffffff;
  padding: 20px 0 0 25px;
}

.intro {
  display: flex;
  flex-direction: column;
}
.intro__background {
  background-color: #463558;
  color: #ffffff;
}

.intro__image,
.intro__text {
  flex-basis: 50%;
}

.intro__text {
  background-color: #463558;
  color: #ffffff;
  padding: 30px 25px;
}

.intro__item {
  padding-bottom: 10px;
}

h2 {
  font-size: 20px;
  color: #161718;
  font-weight: 600;
  text-align: center;
  padding: 20px;
}

h3 {
  font-size: 18px;
  color: #161718;
  font-weight: 600;
  text-align: center;
  padding: 15px 0;
}

.columns {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-bottom: 20px;
}
.columns__icon {
  width: 20px;
  height: auto;
  padding-bottom: 10px;
}
.columns__column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  text-align: center;
}
.columns__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buttons {
  display: flex;
  justify-content: center;
}

.buttons__button {
  padding: 10px 20px;
  background-color: #463558;
  color: #ffffff;
  display: inline-block;
  border-radius: 15px;
  text-decoration: none;
  font-weight: 700;
  margin-bottom: 45px;
}
.buttons__button:hover {
  color: #463558;
  background-color: #ffffff;
  border: 2px solid #463558;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
.grid__head {
  padding-bottom: 15px;
}
.grid__text {
  height: 135px;
  background-color: #463558;
  padding: 15px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: #ffffff;
}
.grid__padding-bottom {
  padding-bottom: 15px;
}

@media screen and (min-width: 960px) {
  .grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.footer {
  background-color: #463558;
}